@import "../../variables";
.applied-color{ 
    padding: 0.4vw; 
    font-size: 0.8em;
}

.applied-colors-container{ 
    display: flex; 
    justify-content: start; 
    flex-wrap: wrap; 
    overflow: auto;
    align-content: baseline;
}
.try-colors-container{ 
    display: flex;
    justify-content: space-between;
    height: -webkit-fill-available;
}
.try-card-holder{
    font-size: 1em;
}
#try-background {
    cursor: pointer; 
    padding: $padding-l1;
    height: -webkit-fill-available;
}
.try-title-holder{  
    text-align: center;
    padding-bottom: 12px 
}
.reset-try-card-btn{
    float: right;
    cursor: pointer; 
}
#try-text{
    font-size: 0.9em;
    cursor: pointer; 
}
.try-button{
    margin-right: $padding-l2;
    font-size: 0.9em;
    padding: $padding-l3; 
    border-radius: 4px;
    font-weight: 600;
}
.applied-colors-header{
    margin-top: $padding-l2;
    display: flex; 
    justify-content: space-between; 
    align-items: center; 
}